import React, { Fragment, useState, useEffect } from "react";
import styled, { keyframes } from "styled-components";
import { fadeIn } from "react-animations";
import ReactGA from 'react-ga';
import Header from "./Header";
import Footer from "./Footer";
import Divider from "./Divider";
import Thumbnail from "./Thumbnail";
import ServicesList from "./ServicesList";
import BookOnlineButton from "./BookOnlineButton";
import "./styles.css";

import sun from "./icons/sun.gif";

const fadeInAnimation = keyframes`${fadeIn}`;

const FadeInDiv = styled.div`
  animation: 2s ${fadeInAnimation};
`;

const mapURL = 'https://www.google.com/maps/place/%D0%9A%D0%BE%D1%81%D0%BC%D0%B5%D1%82%D0%BE%D0%BB%D0%BE%D0%B3+%D0%BD%D0%B0+%D0%A1%D0%BE%D0%B2%D0%B5%D1%82%D1%81%D0%BA%D0%BE%D0%B9/@47.8172417,35.1688245,17z/data=!3m1!4b1!4m5!3m4!1s0x0:0x151763ce1e99e96!8m2!3d47.8172417!4d35.1710132?hl=uk';

const App = () => {
  let isMobile;
  const [showMap, toggleMap] = useState(false);
  const handleMapClick = () => window.open(mapURL,'_blank');
  const screenWidth = parseInt(window.screen.width);
  
  if (screenWidth < 1280) {
    isMobile = true;
  } else {
    isMobile = false;
  }
  
  useEffect(() => {
    ReactGA.initialize('G-2VBJR3DWWS');
    ReactGA.pageview(window.location.pathname + window.location.search);
  })
  return (
    <Fragment>
      <div className="app">
        <FadeInDiv>
          {!isMobile && <img src={sun} className="landing_view__sun" alt="sun" />}
          <Header />
        </FadeInDiv>
        <FadeInDiv>
          <Thumbnail isMobile={isMobile} />
          {/* <BookOnlineButton /> */}
          <Divider />
          <ServicesList isMobile={isMobile} />
          <Footer handleMapClick={handleMapClick} />
        </FadeInDiv>
      </div>
    </Fragment>
  );
};
export default App;
